@each $variant, $color in (primary: $blue, secondary: $gray-lighter, success: $green, info: $cyan, warning: $yellow, danger: $red) {
  .modal-#{$variant} {

    .modal-content {
      border-color: $color;
    }

    .modal-header {
      color: #fff;
      background-color: $color;
    }
  }
}



